import { Component, OnInit } from '@angular/core';
import { ValidationService} from './services/validation.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import { AuthService} from './services/auth.service';
import { Router} from '@angular/router';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Naijapedia';
  client: any;
 constructor(public authService: AuthService, private flashMessage: FlashMessagesService){}
 

 ngOnInit(): void{
  this.client = this.authService.loadUser();
  console.log(this.client);
  }
  
  onLogOut(){
	this.authService.logout();
	    this.flashMessage.show('Logged out successfully',{cssClass: 'alert-success', timeout: 3000});

  }
}
