import { Component, OnInit } from '@angular/core';
import { ValidationService} from '../services/validation.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import { AuthService} from '../services/auth.service';
import { Router} from '@angular/router';


@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

	name: String;
	email: String;
	password: String;
	
  constructor(private validateService: ValidationService,
  private flashMessage: FlashMessagesService,
    private authService: AuthService,
	private router: Router

  ) { }

  ngOnInit(): void {
  }

  
  
  onRegisterSubmit(){
  const user={
  name: this.name,
  email: this.email,
  password: this.password
  }
  
  
  if(!this.validateService.validateRegister(user)){
  this.flashMessage.show('Please fill in all fields',{cssClass: 'alert-danger', timeout: 3000});
  return false;
  }
  
  if(!this.validateService.validateEmail(user.email)){
  this.flashMessage.show('Please fill in correct email',{cssClass: 'alert-danger', timeout: 3000});
  return false;
  }
  
  this.authService.registerUser(user).subscribe( data =>{
  console.log(data);
  if(false){
    this.flashMessage.show('Email already exist',{cssClass: 'alert-danger', timeout: 3000});
	return false;
  }
  else if(false){
    this.flashMessage.show('Successfully registered',{cssClass: 'alert-success', timeout: 3000});
 this.router.navigate(['/signin']);
  return false;
  }
  else{
    this.flashMessage.show('Server Error',{cssClass: 'alert-danger', timeout: 3000});
return false;
  }
  
	
  
  });
  
  }
  
  
  
}
