import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from './home/home.component'
import {ArticleComponent} from './article/article.component'
import {ArticleEditComponent} from './article-edit/article-edit.component'
import {ReferenceEditComponent} from './reference-edit/reference-edit.component'
import {SubtopicEditComponent} from './subtopic-edit/subtopic-edit.component'
import {SearchComponent} from './search/search.component'
import {ArticleAddComponent} from './article-add/article-add.component'
import {PrototypingComponent} from './prototyping/prototyping.component'
import {RecentComponent} from './recent/recent.component'
import {SigninComponent} from './signin/signin.component'
import { SignupComponent } from './signup/signup.component';
import {AuthGuard} from './guards/auth.service';




const routes: Routes = [
{path: '', component: HomeComponent},
{path: 'home', component: HomeComponent},
{path: 'article/:id', component: ArticleComponent},
{path: 'articleEdit/:id', component: ArticleEditComponent, canActivate:[AuthGuard]},
{path: 'referenceEdit/:id', component: ReferenceEditComponent, canActivate:[AuthGuard]},
{path: 'subtopicEdit/:id', component: SubtopicEditComponent, canActivate:[AuthGuard]},
{path: 'search', component: SearchComponent},
{path: 'articleAdd', component: ArticleAddComponent, canActivate:[AuthGuard]},
{path: 'prototyping', component: PrototypingComponent},
{path: 'recent', component: RecentComponent},
{path: 'signin', component: SigninComponent},
{path: 'signup', component: SignupComponent}




];

@NgModule({ 
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
